import { Injectable } from '@angular/core';
import { TreeviewItem, TreeviewI18n } from 'ngx-treeview';
import { I18n } from './i18n';

@Injectable()
export class DefaultTreeviewI18n extends TreeviewI18n {
    constructor(
        protected i18n: I18n
    ) {
        super();
    }

    getText(checkededItems: TreeviewItem[], isAll: boolean): string {
        if (isAll) {
            return this.i18n.language === 'en' ? 'All' : 'Tt c';
        }

        switch (checkededItems.length) {
            case 0:
                return this.i18n.language === 'en' ? 'Select options' : 'Chn mc';
            case 1:
                return checkededItems[0].text;
            default:
                return this.i18n.language === 'en'
                    ? `${checkededItems.length} options selected`
                    : `${checkededItems.length} mc  c chn`;
        }
    }

    allCheckboxText(): string {
        if (this.i18n.language === 'en') {
            return 'All';
        } else {
            return 'Tt c';
        }
    }

    filterPlaceholder(): string {
        if (this.i18n.language === 'en') {
            return 'Filter';
        } else {
            return 'Lc';
        }
    }

    filterNoItemsFoundText(): string {
        if (this.i18n.language === 'en') {
            return 'No items found';
        } else {
            return 'Khng c mc no c tm thy';
        }
    }

    tooltipCollapseExpand(isCollapse: boolean): string {
        return isCollapse
            ? this.i18n.language === 'en' ? 'Expand' : 'M rng'
            : this.i18n.language === 'en' ? 'Collapse' : 'Thu li';
    }
}



